		watch: {
			fkFileName: function(newVal, oldVal) {
				var _this = this;
				if(this.fkFileName!=''){
					this.isShowBox = true;
				}
				
				_this.submitAjax('appmodule/appFileMapper/selectAppByName?fkFileName=' + newVal, null, 'GET',
